// =============================================================================
// x402 Agent Detail API Routes
// =============================================================================

import { NextRequest, NextResponse } from 'next/server';
import { requireAuth } from '@/lib/auth';
import {
  getAgent,
  getRouteConfigs,
} from '@/db/x402-agents';
import {
  listPayments,
  getRevenueShares,
} from '@/db/x402-agents';
import {
  computeAgentSummary,
  computeRevenueSummary,
} from '@/lib/x402-agents/aggregation';

/**
 * GET /api/v1/x402/agents/:id
 * Get agent details with full stats and route configs
 */
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ id: string }> }
) {
  const { id: agentId } = await params; // Next.js 15+ requires awaiting params
  
  try {
    const authResult = await requireAuth(request, 'user');
    if ('response' in authResult) {
      return authResult.response;
    }

    const { apiKey } = authResult;
    const ownerUserId = apiKey.userId || apiKey.id;

    // Get agent
    const agent = await getAgent(agentId);
    if (!agent) {
      return NextResponse.json(
        { error: 'Agent not found', code: 'NOT_FOUND' },
        { status: 404 }
      );
    }

    // Verify ownership
    if (agent.owner_user_id !== ownerUserId) {
      return NextResponse.json(
        { error: 'Forbidden', code: 'FORBIDDEN' },
        { status: 403 }
      );
    }

    // Get route configs
    const routeConfigs = await getRouteConfigs(agentId);

    // Get payments and revenue shares
    const payments = await listPayments({ agentId });
    const revenueShares = await getRevenueShares(agentId);

    // Calculate summaries
    const summary = computeAgentSummary({
      payments,
      revenueShares,
      agentId,
    });

    const revenueSummary = computeRevenueSummary({
      payments,
      revenueShares,
      agentId,
    });

    // Filter to last 30 days for 30d stats
    const thirtyDaysAgo = new Date();
    thirtyDaysAgo.setDate(thirtyDaysAgo.getDate() - 30);
    const recentPayments = payments.filter(
      p => p.status === 'confirmed' && new Date(p.created_at) >= thirtyDaysAgo
    );
    const recentRevenueShares = revenueShares.filter(
      rs => new Date(rs.created_at) >= thirtyDaysAgo
    );

    const summary30d = computeAgentSummary({
      payments: recentPayments,
      revenueShares: recentRevenueShares,
      agentId,
    });

    return NextResponse.json({
      agent,
      routeConfigs,
      summary: {
        totalVolume: summary.totalVolume,
        totalPayments: summary.totalPayments,
        totalFees: summary.totalFees,
        totalRevenueShared: summary.totalRevenueShared,
        volume30d: summary30d.totalVolume,
        payments30d: summary30d.totalPayments,
      },
      revenue: revenueSummary,
    });
  } catch (error: any) {
    console.error('[x402/agents/:id] GET error:', error);
    return NextResponse.json(
      {
        error: error.message || 'Failed to get agent',
        code: 'INTERNAL_ERROR',
      },
      { status: 500 }
    );
  }
}

